<template>
    <section class="content">
        <div class="container-fluid">
            <div class="row">

                <div class="col-12">

                    <div class="card" >
                        <div class="card-header">
                            <h3 class="card-title">Ticket Report</h3>
                            <div class="card-tools">

                            </div>
                        </div>

                        <div class="card-body p-2">
                            <form autocomplete="off" @submit.prevent="getResults()">
                                <div class="form-row">
                                    <div class="col col-2">
                                        <v-select label="name" :get-option-label="getLabel"
                                            :reduce="(option) => option.id" :options="search_machines"
                                            placeholder="Enter machines ..." v-model="search.machine_id"
                                            name="machine_id">
                                        </v-select>
                                    </div>
                                    <div class="col col-2">
                                        <input type="text" placeholder="Enter ticket ..." v-model="search.ticket"
                                            name="ticket" class="form-control">
                                    </div>
                                    <div class="col col-2">
                                        <v-select label="site_name" :reduce="(option) => option.id"
                                            :options="search_sites" placeholder="Select Site..."
                                            v-model="search.site_id" name="site_id">
                                        </v-select>
                                    </div>
                                    <div class="col col-2">
                                        <v-select label="site_name" :options="['Open', 'Closed']"
                                            placeholder="Select Status..." v-model="search.status" name="status">
                                        </v-select>
                                    </div>
                                    <div class="col col-2">
                                        <v-select label="name" :options="created_users" :reduce="(option) => option.id"
                                            placeholder="Created by..." v-model="search.created_by" name="created_by">
                                        </v-select>
                                    </div>
                                    <div class="col col-2">
                                        <v-select label="name" :options="['Red','Solid Red','Yellow','Green']"
                                            placeholder="Choose Color..." v-model="search.color" name="color">
                                        </v-select>
                                    </div>
                                    <div class="col col-2 pt-2">
                                       <datetime value-zone="Asia/Kolkata" v-model="search.created_from" input-class="form-control" placeholder="Created from date" />
                                    </div>
                                    <div class="col col-2 pt-2">
                                       <datetime value-zone="Asia/Kolkata" v-model="search.created_to" input-class="form-control" placeholder="Created to date"/>
                                    </div>
                                    <div class="col col-2 pt-2">
                                        <v-select label="site_name" :options="['Major', 'Minor']"
                                            placeholder="Select Complaint Nature..." v-model="search.complaint_nature" name="complaint_nature">
                                        </v-select>
                                    </div>
                                    <div class="col col-4 pt-2">
                                       <!--  <MultiRangeSlider baseClassName="multi-range-slider" :min="0"
                                            :max="100" :step="5" :ruler="true" :label="true"
                                            :minValue="search.tat_from" :maxValue="search.tat_to"
                                            @input="UpdateValues" /> -->



  <fieldset style="max-width: 400px; margin: auto; text-align: left;  ">
    <div style="display: flex; justify-content: space-between; text-align: center; height: 39px;">
      <span
        style="
          display: inline-block;
          padding: 5px;
          border: solid 1px;
          border-radius: 5px;
          width: 100px;
          margin: 3px;

        "
        >{{ search.tat_from }}</span
      >
      <span style="top: 25%; position: relative;">TAT</span>
      <span
        style="
          display: inline-block;
          padding: 5px;
          border: solid 1px;
          border-radius: 5px;
          width: 100px;
          margin: 3px;
        "
        >{{ search.tat_to }}</span
      >
    </div>

    <MultiRangeSlider
      baseClassName="multi-range-slider-bar-only"
      :minValue="search.tat_from"
      :maxValue="search.tat_to"
      :max="100"
      :min="0"
      :step="1"
      :rangeMargin="0"
      @input="UpdateValues"
    />
  </fieldset>









                                    </div>
                                    <div class="col pt-2">
                                    <button type="submit" class="btn btn-success">Search</button>
                                    <button type="button" class="btn btn-info ml-2" @click="reset_filter()">Reset</button>
                                </div>
                                </div>
                            </form>
                        </div>

                        <!-- /.card-header -->
                        <div class="card-body table-responsive p-0 ticketTable">
                            <table class="table table-hover">
                                <thead>
                                    <tr>
                                        <th>Ticket No </th>
                                        <th>Machine Name</th>
                                        <th>Machine Far No</th>
                                        <th>Site</th>
                                        <th>Created By</th>
                                        <th>Created Date</th>
                                        <th>Expected date</th>
                                        <th>Revised <br> Expected date</th>
                                        <th>Resolution date</th>
                                        <th>Ticket Status</th>
                                        <th>Complaint Nature</th>
                                        <th>TAT</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr v-for="ticket in Tickets.data" :key="ticket.id" :class="ticket.row_color">
                                        <td>{{ ticket.ticket_no }}</td>
                                        <td class="text-capitalize">{{ ticket.machine.name }}</td>
                                        <td @click="view_site_details(ticket.machine)">{{ ticket.machine.far_no }}</td>
                                         <td>{{ ticket.site ? ticket.site.site_name:'-' }}</td>
                                        <td>{{ ticket.created_by_name }}</td>
                                        <td>{{ ticket.created_at | myDate }}</td>
                                        <td>{{ ticket.likely_date | myDate }}</td>
                                        <td>{{ ticket.reversion_date|myDate }}</td>
                                        <td>{{ ticket.actual_date   }}</td>
                                        <td >{{ticket.status.toLowerCase()=='created by user'?'Open':ticket.status}}</td>
                                        <td>{{ticket.complaint_nature}}</td>
                                        <td>{{ticket.actual_date?ticket.tat:'-'}}</td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                        <!-- /.card-body -->
                        <div class="card-footer">
                            <pagination :data="Tickets" @pagination-change-page="getResults" :limit="5"></pagination>
                        </div>
                    </div>
                    <!-- /.card -->
                </div>
            </div>





        </div>

        <MachinePop id="MachineSiteModal2" :machine="pmModalData"/>

    </section>
</template>

<script>
import MultiRangeSlider from "multi-range-slider-vue";
import "multi-range-slider-vue/MultiRangeSliderBlack.css";
import "multi-range-slider-vue/MultiRangeSliderBarOnly.css";

import VueTagsInput from '@johmun/vue-tags-input';
export default {
    components: {
        VueTagsInput,
        MultiRangeSlider,
        MachinePop: () => import('../MachineFarPop.vue'),
    },
    data() {
        return {
            pmModalData:'',
            created_users: [],
            search_machines: [],
            search_sites: [],
            search: {
                page: 1,
                machine_id: '',
                site_id: '',
                created_by: '',
                created_from: '',
                created_to: '',
                tat_from: 0,
                tat_to: 90,
                color:'',
                ticket:''
            },
            categories: [],
            sub_categories: [],
            sites: [],
            selected_machine: '',
            users: [],
            machines: [],
            Tickets: {},


            json_fields: {
                'Ticket no': 'ticket_no',
                'Last Name': 'last_name',
                'Email': 'email',
                'Type': 'type',
                'Country Code': 'country_code',
                'Phone Number': 'phone_number',
                'VSD ID': 'vsd_id',
                'License Number': 'license_number',
            },
            json_meta: [
                [{
                    key: "charset",
                    value: "utf-8",
                },],
            ],

        }
    },
    methods: {
        view_site_details(data) {
            this.pmModalData = data
            $('#MachineSiteModal2').modal('show');
        },
        reset_filter(){
            this.search= {
                page: 1,
                machine_id: '',
                site_id: '',
                created_by: '',
                created_from: '',
                created_to: '',
                tat_from: 0,
                tat_to: 90,
                color:'',
                ticket:''
            }
            this.getResults()
        },
        UpdateValues(e) {
            this.search.tat_from = e.minValue;
            this.search.tat_to = e.maxValue;
        },
        // excel
        async export_csv() {
            const response = await axios.get('api/ticket/export');

            return response.data.data;
        },
        startDownload() {
            Swal.fire({
                title: 'Please Wait !',
                html: 'Data populating', // add html attribute if you want or remove
                allowOutsideClick: false,
                onBeforeOpen: () => {
                    Swal.showLoading()
                },
            });
        },
        finishDownload() {
            Swal.close()
        },



        getLabel(val) {
            if (typeof val === 'object') {
                return val.far_no + ' - ' + val.name;
            } else {
                return val;
            }
        },
        getResults(page = 1) {
            this.$Progress.start();
            this.search.page = page
            axios.get('/api/ticket', {
                params: this.search
            }).then(({
                data
            }) => (this.Tickets = data.data));
            this.$Progress.finish();
        },
        loadTickets() {

                axios.get("/api/ticket", {
                    params: this.search
                }).then(({
                    data
                }) => (this.Tickets = data.data));

        },

    },
    created() {
        this.loadTickets();
    },
    beforeCreate() {
        axios.get("api/get_pre_machine")
            .then((res) => {
                this.categories = res.data.data.Category;
                this.sites = res.data.data.sites;
            })
        axios.get("/api/initial_ticket").then(response => {
            this.users = response.data.data.vendors;
            this.machines = response.data.data.machines;
            this.search_machines = response.data.data.machines;
            this.search_sites = response.data.data.sites;
            this.created_users = response.data.data.created_users;
        }).catch(() => console.warn('Oh. Something went wrong'));
    },
    watch: {
        async 'form.sub_category_id'(n, o) {
            if (n) {
                this.get_machine()
            }
        },
        async 'form.site_id'(n, o) {
            if (n) {
                this.get_machine()
            }
        },

        'form.category_id': {
            handler: function (n, o) {

                if (n) {
                    axios.get("api/category/" + n)
                        .then((res) => {
                            this.sub_categories = res.data.data;
                        })
                    this.get_machine()
                }
            },
            deep: true,
            initial: true
        },

    },
}
</script>
<style>
.bg-red {
  background-color: #ef5563 !important;
}
.bg-green {
  background-color: #70ed70 !important;
}
.bg-yellow {
  background-color: #f1c84c !important;
}
</style>
